import React from 'react';
import './components/particles';

const particlesJS = window.particlesJS;

fetch('/assets/data/home.json')
    .then(home => home.json())
    .then(particles => particlesJS.load('particles-js', particles))
    .catch(err => console.log(err));

const Home = () => {
    return <main />;
};

export default Home;
